import React, { useState,useEffect } from 'react'
import RcSlider,{SliderTooltip} from 'rc-slider'
import 'rc-slider/assets/index.css';
// background : 'linear-gradient(90deg, #CE5E5E 82.22%, #59AE99 89.24%, #59AE99 100%)'
export default function Slider({start,min,max,value,defaultValue,onSlide,className,trackStyle = {},railStyle = {},handleStyle = {}}){
  const slide = value => {
    onSlide && onSlide(value)
  }
  return (
    <RcSlider
        min={min}
        max={max}
        value={value}
        className={className}
        onChange={onSlide}
        defaultValue={defaultValue}
        trackStyle={{...trackStyle}}
        handleStyle={{...handleStyle}}
        railStyle={{...railStyle}}
      />
  )
}