import React from "react";
import { Pie, PieChart, Cell,Label } from "recharts";

export default function Gauge({}){
  const data = [{ value: 527.65 }, { value: 602.35 },{value : 300}];
  return (
    <div style={{ width: 108, height: 92 }}>
      <PieChart height={108} width={92}>
        <defs>
          <linearGradient
            id="colorUv"
            x1="0"
            y1="0"
            x2="0"
            y2="100%"
            gradientUnits="userSpaceOnUse"
          >
            <stop offset="0.17" stopColor="#43608D" />
            <stop offset="100" stopColor="#3F4D8B" />
          </linearGradient>
        </defs>
        <Pie
          startAngle={220}
          endAngle={-40}
          innerRadius="65%"
          data={data}
          dataKey="value"
          labelLine={true}
          blendStroke
          isAnimationActive={true}
        >
        
        <Label
                value={2.5}
                position="centerBottom"
                offset={0}
                className="gauge-label"
                fill ='#E0ECFF'
                fontSize="24px"
              />
          <Cell fill="url(#colorUv)" />
          <Cell fill="#eaeaea" />
        </Pie>
      </PieChart>
    </div>
  )
}