import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import UnderlineText from "../../../components/UnderlineText/UnderlineText";
import DeriNumberFormat from "../../../utils/DeriNumberFormat";
import { inject, observer } from 'mobx-react';
import Type from '../../../model/Type';
import Intl from '../../../model/Intl'
import classNames from 'classnames';

const Wrapper = styled.div`
  display : flex;
  height: 96px;
  align-items: center;
  justify-content: space-between;
  margin: 0 32px;

  .m-col {
    display : flex;
    align-items: flex-start;
    flex-direction: column;
    margin-right : 48px;
    justify-content: space-between;
    height : 46px;
    .col-value {
      font-size : 20px;
      color : #E0ECFF;
      height :24px;
    }
    .col-value.up {
      color : #59AE99;
    } 
    .col-value.down {
      color : #CE5E5E;
    }
  }
`
const getLang = key => {
  return Intl.get('trade', key)
}
const fileds = {
  'mark-price' : {text : getLang('mark-price'),value : '',prefix : '$',decimal : 2,type : 'number'},
  'index-price' : {text : getLang('index-price'),value : '',prefix : '$',decimal : 2,type : 'number'},
  'dvol' : {text : getLang('dvol'),tip : getLang('dvol-tip'),value : '',suffix : '%',decimal : 2},
  'daily-funding' : {text : getLang('daily-funding'),value : '',suffix : '%',decimal : 2},
  '24h-change' : {text : getLang('24h-change'),value : '',type : 'number'},
  'total-net-position' : {text : getLang('total-net-position'),tip : getLang('total-net-position-tip'),value : '',decimal : 2,type : 'number'},
  'margin-req' :{text : getLang('margin-req'),value : '',tip : getLang('margin-req-tip')},
  'fee' : {text : getLang('fee'),value : ''}
}
function TradeInfo({trading}){
  const [columns, setColumns] = useState([])
  useEffect(() => {
    if(trading.fundingRate){
      let cols = []
      if(Type.isFuture){
        cols = [
          {...fileds['mark-price'],value : trading.markPrice} ,
          {...fileds['index-price'],value : trading.index},
          {...fileds['daily-funding'],value : trading.fundingFee,tip : trading.fundingFeeTip},
          {...fileds['24h-change'],value :  trading.symbolInfo.markPrice,className : trading.symbolInfo.volumn24h > 0 ? 'up' : 'down'},
          {...fileds['total-net-position'],value : trading.netPositionNutual}
        ]
      } else {
        cols = [
          {...fileds['mark-price'],value : trading.markPrice} ,
          {...fileds['dvol'],text : trading.symbolInfo.underlier,value : trading.dvol},
          {...fileds['daily-funding'],value : trading.fundingFee,tip : trading.fundingFeeTip},
          {...fileds['24h-change'],value : trading.symbolInfo.markPrice,className : trading.symbolInfo.volumn24h > 0 ? 'up' : 'down'},
          {...fileds['total-net-position'],value : trading.netPositionNutual},
          {...fileds['margin-req'],value : trading.marginReqs},
          {...fileds['fee'],value : trading.fee},
        ]
      }
      setColumns(cols)
    }
  }, [trading.fundingRate,trading.volatility,trading.fee,trading.marginReqs,trading.netPositionNutual,trading.dailyFunding,trading.symbolInfo.markPrice])
  return(
    <Wrapper>
      {columns.map((col,index) => (
        <div className='m-col' key={index}>
          {col.tip ? <UnderlineText id={col.id} text={col.text} tip={col.tip} multiline/> : col.text}
          <div className={classNames('col-value',col.className)}>
            { col.type === 'number' 
            ? <DeriNumberFormat  value = {col.value} prefix={col.prefix} suffix={col.suffix} decimalScale={col.decimal || 2}/> 
            : col.value }
          </div>
        </div>
      ))}
  </Wrapper>
  )
}
export default inject('trading')(observer(TradeInfo))